import React from "react";

class SearchBar extends React.Component {
  state = { term: "" };
  onInputChange = (event) => {
    this.setState({ term: event.target.value });
  };

  onFormSubmit = (event) => {
    event.preventDefault();
    this.props.onFormSubmit(this.state.term);
    //TODO: call callback from parent component
  };
  render() {
    return (
      <div className="search-bar ui container">
        <form onSubmit={this.onFormSubmit} className="ui form">
          <label>Video Search</label>
          <input
            type="text"
            value={this.state.term}
            onChange={this.onInputChange}
          />
        </form>
      </div>
    );
  }
}

export default SearchBar;
//uncontrolled  input: so that react is the one changing the value of the input: dynamic inputs
//if we dont put an onChange even handler, the input value wont change coz it will be fixed to the default
